@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
*{font-family:'Open Sans Condensed', sans-serif}body{background-image:url(/static/media/fondo.6eda72ff.png)}.container{background:white;margin:0 auto;max-width:700px;width:90%}

.warning{background:#F3C1BA;color:#ff0000;font-size:12px;padding:10px 15px;text-align:center}.input{display:flex;justify-content:space-between;margin-bottom:10px}.input select{background:#e4e4e4;border-style:none;border-radius:5px;cursor:pointer;padding:5px 10px;width:80%;-moz-appearance:none;-webkit-appearance:none;appearance:none}.input select:focus{border:1px solid #472D30}.input div{width:80%;display:flex;justify-content:space-around}.input div input{cursor:pointer;margin-right:10px}button{background:#E26D5C;border:none;border-radius:5px;color:white;cursor:pointer;letter-spacing:5px;padding:10px 15px;width:100%}button:hover{background:rgba(226,109,92,0.8)}

.mostrarCotizacion{text-align:center}.resumen{background:#723D46;margin-top:15px;padding:10px}.resumen h2{color:white}.resumen p{color:white}.total{background:#D7D9D7;color:#211B22;margin-top:10px;padding:5px}

h1{background:#472D30;color:#e4e4e4;padding:10px;text-align:center}main{padding:10px 50px}

